
// Fonts
@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600);

// Variables
@import "partials/variables", "partials/mixins";

// Bootstrap
@import "normalize.css";

.row {
	margin: 0px;
}

a {
	cursor: pointer;
}

.navbar-default {
	box-shadow: 0px 0px 8px 1px grey;
}

.language-selector img {
	max-width: 25px;
}

.item {
	margin: 0 auto;
	text-align: center;
}

.item img {
	margin: 0 auto;
	padding: 0;
	text-align: center;
}

.panel > .panel-heading {
	border-color: $blue!important;
	background-color: $blue!important;
	color: #fff;
}

.services-row {
	padding-top: 50px;
}

.work-kinds table {
	width: 100%;
}

.btn-success {
	background-color: $green;
	border-bottom: 4px solid darken($green, 5%);
}

.primary {
	color: $primary;
}

.info {
	color: $info;
}

.success{
	color: $success;
}

.warning{
	color: $warning;
}

.danger{
	color: $danger;
}

.heading-row {
	background-color: $bg-color;
	max-height: 70px;
}

.icon img {
	max-height: 250px;
}

.footer {
	min-height: 200px;
	background-color: $info;
}

.footer div {
	margin-top: 90px;
}

.reach-me h3 {
	color: #fff;
	text-shadow: 0 -1px 0 rgba(0, 0, 0, .3);
	padding: 0;
	margin: 4px 0 0 0;
}